body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin: 0px;
    min-height: 100vh;
}
div.modals.active {
    background-color: rgba(0,0,0, .95)
}

#react-root {
    height: 100%;
}
.app {
    min-height: 100vh;
    position: relative;
}
.alert {
    text-align: center;
    border: 2px solid red;
    padding: 20px 10px;
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: 2px;
}

.log-in, .log-out, .register {
  background-color: #00acb0;
  border-radius: 4px;
  color: #f8f8f8;
  min-height: 50px;
  min-width: 100px;
  outline:0;
  cursor: pointer;
}

.log-in:hover, .log-out:hover, .register:hover {
  background-color: #008183;
}

#loginPage h1, #registerPage h1 {
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: 3px;
}
#loginPage div.ui.label, #registerPage div.ui.label {
    width: 100px;
    text-align: center;
    font-weight: 300;

}

/* This line makes the "Register" and "Login" buttons look like links */
.link-button {
    background:none;
    color:inherit;
    border:none;
    padding:0;
    font: inherit;
    outline:0;
    border-bottom:1px solid #444; 
    cursor: pointer;
}


div.ui.dimmer {
    max-width: 100vw !important;
}

#addPayment div.ui.basic.label {
    min-width: 125px;
    font-weight: 400;
    letter-spacing: 1px;
}